import { Injectable } from '@angular/core';
import { environment } from '../environment/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FacebookPixelService {
  private apiVersion: string = environment.facebook.apiVersion;
  private pixelId: string = environment.facebook.pixelId;
  private accessToken: string = environment.facebook.token;

  private url = `https://graph.facebook.com/${this.apiVersion}/${this.pixelId}/events?access_token=${this.accessToken}`;

  constructor(private http: HttpClient) { }

  sendEvent(event: any): Observable<any> {
    const headers = new HttpHeaders({
      'Content-Type': 'application/json'
    });

    return this.http.post(this.url, event, { headers: headers });
  }
}
