import { HttpClient } from '@angular/common/http';
import { Component, Input, OnInit } from '@angular/core';
import { AngularFireDatabase } from '@angular/fire/compat/database';
import { AngularFireFunctions } from '@angular/fire/compat/functions';
import { AngularFireStorage } from '@angular/fire/compat/storage';
import { TranslateService } from '@ngx-translate/core';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';

import { finalize, take } from 'rxjs/operators';
declare const gtag: Function;

@Component({
  selector: 'app-certificate',
  templateUrl: './certificate.component.html',
  styleUrls: ['./certificate.component.scss']
})
export class CertificateComponent implements OnInit {
  @Input() name: string = 'Nombre y apellido';
  @Input() iqValue: number = 0;
  email: string;
  downloadURL: string = '';
  date: Date = new Date();
  certificateNumber: string = '';
  loadingCertificate: boolean = true;

  constructor(
    private storage: AngularFireStorage,
    private db: AngularFireDatabase,
    private http: HttpClient,
    private translate: TranslateService
  ) {}

  ngOnInit() {
    this.generateCertificateNumber();
    this.loadUserInfo();
  }

  generateCertificateNumber(): void {
    const randomNumber = Math.floor(Math.random() * 1000000);
    const year = this.date.getFullYear();
    this.certificateNumber = `${randomNumber}-${year}`;
  }

  loadUserInfo(): void {
    const userInfo = localStorage.getItem('userInfo');
    if (userInfo) {
      const user = JSON.parse(userInfo);
      this.name = user.name;
      this.iqValue = user.result;
      this.email = user.email;
      setTimeout(() => { this.generateAndUploadPDF(); }, 1000);
      localStorage.removeItem('userInfo');
      this.createSubscription();
    }
  }

  async generateAndUploadPDF(): Promise<void> {
    const certificateElement = document.getElementById('certificate');
    if (certificateElement) {
      const canvas = await html2canvas(certificateElement, {
        scale: 2,
        useCORS: true
      });

      const imgData = canvas.toDataURL('image/jpeg', 1.0);

      const pdf = new jsPDF('l', 'mm', 'a4');

      const pdfWidth = pdf.internal.pageSize.getWidth();
      const pdfHeight = pdf.internal.pageSize.getHeight();

      const imgProps = pdf.getImageProperties(imgData);
      const imgWidth = imgProps.width;
      const imgHeight = imgProps.height;

      const ratio = Math.min(pdfWidth / imgWidth, pdfHeight / imgHeight);

      const newWidth = imgWidth * ratio;
      const newHeight = imgHeight * ratio;

      const x = (pdfWidth - newWidth) / 2;
      const y = (pdfHeight - newHeight) / 2;

      pdf.addImage(imgData, 'JPEG', x, y, newWidth, newHeight);

      const pdfOutput = pdf.output('blob');

      const filePath = `certificates/${this.name}-${this.certificateNumber}.pdf`;
      const fileRef = this.storage.ref(filePath);
      const task = this.storage.upload(filePath, pdfOutput);

      task.snapshotChanges().pipe(
        finalize(async () => {
          this.downloadURL = await fileRef.getDownloadURL().toPromise();
          this.loadingCertificate = false;
          const userUid = JSON.parse(localStorage.getItem('user'))['uid'];
          this.db.list(`certificates/${userUid}`).push({
            name: this.name,
            iqValue: this.iqValue,
            certificateNumber: this.certificateNumber,
            date: this.date.toISOString(),
            url: this.downloadURL
          });

          this.db.object(`users/${userUid}`).valueChanges().pipe(take(1)).toPromise().then(user => {
            if (user) {
              return;
            } else {
              this.db.object(`users/${userUid}`).set({
                name: this.name,
                email: this.email,
                registrationDate: this.date.toISOString(),
                role: 2,
                subscriptionActive: true,
                pay: true
              });
            }
          });
          const subject = this.translate.instant('email.emailSubject');
          const text = this.translate.instant('email.emailText', { name: this.name });
          const html = this.translate.instant('email.htmlBody', {name: this.name})

          // Construye el payload con los campos necesarios
          const payloadEmail = {
            email: this.email,
            name: this.name,
            url: this.downloadURL,
            subject: subject,
            text: text,
            html: html
          };
          const urlEmail = 'https://us-central1-testiq-abc9f.cloudfunctions.net/sendCertificateEmail ';
          this.http.post(urlEmail, payloadEmail).subscribe(
            (response: any) => {
              console.log(response);
            }, (error) => {
              console.log(error);
            }
          );
        })
      ).subscribe();
    }
  }


  downloadCertificate(): void {
    if (this.downloadURL) {
      const link = document.createElement('a');
      link.href = this.downloadURL;
      link.download = `certificate_${this.name}_${this.certificateNumber}.pdf`;
      link.target = '_blank';

      // For some mobile browsers, we might need to append the link to the DOM
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    } else {
      console.error('Download URL is not available.');
    }
    this.trackConversion();
  }

  createSubscription() {
    const url ='https://us-central1-testiq-abc9f.cloudfunctions.net/createSubscription'
    const payload = {email: this.email}
    this.http.post(url, payload).subscribe(
      (response: any) => {
        gtag('event', 'conversion', {'send_to': 'AW-11348900696/u9zICOLAn74ZENj2yaMq'});
      }, (error) => {
      console.error('Errore nella creazione dell\'abbonamento:', error);
    });
  }

  trackConversion(): void {
    gtag('event', 'conversion', {
      'send_to': 'AW-11348900696/l3PUCK3q_bwZENj2yaMq',
      'value': 0.5,
      'currency': 'EUR',
      'transaction_id': '',
      'event_callback': () => {
      }
    });
  }
}
