import { Component, OnInit } from '@angular/core';
import { FacebookPixelService } from '../../services/facebook-pixel.service';
import * as crypto from 'crypto-js';
import { IpServiceService } from '../../services/ip-service.service';
import { AngularFireDatabase } from '@angular/fire/compat/database';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { ChartConfiguration, ChartData, ChartOptions } from 'chart.js';

@Component({
  selector: 'app-success',
  templateUrl: './success.component.html',
  styleUrl: './success.component.scss'
})
export class SuccessComponent implements OnInit {
  iqResult: number = 0;
  name: string;
  email: string;

  public lineChartData: ChartConfiguration<'line'>['data'] = {
    labels: [
      '(> 69) IQ',
      '(70-79) IQ',
      '(80-89) IQ',
      '(90-109) IQ',
      '(110-119) IQ',
      '(120-129) IQ',
      '(130-144) IQ',
      '(145-160) IQ'
    ],
    datasets: [
      {
        data: [0.1, 2, 14, 34, 34, 14, 2, 0.1],
        label: '%',
        fill: true,
        tension: 0.5,
        borderColor: '#476597',
        backgroundColor: 'rgba(71, 101, 151, .3)'
      }
    ]
  };

  public lineChartOptions: ChartOptions<'line'> = {
    responsive: true,
  };
  public lineChartLegend = false;

  constructor(private fbPixelService: FacebookPixelService,
              private ipService: IpServiceService,
              private router: Router,
              ) { }

  ngOnInit(): void {
    const userInfo = JSON.parse(localStorage.getItem('userInfo'));

    if(userInfo){
      this.iqResult = userInfo['result'];
      this.name = userInfo['name'];
      this.email = userInfo['email'];
      this.ipService.getIp().subscribe((response: any) => {
        const ipAddress = response.ip;
        const userAgent = navigator.userAgent;
        this.sendPurchaseEvent(ipAddress, userAgent);
        this.sendSubscribeEvent(ipAddress, userAgent);

      });
    }else{
      this.router.navigate([""]);
    }
  }

  sendPurchaseEvent(ipAddress: string, userAgent: string): void {
    const hashedEmail = crypto.SHA256(this.email).toString(crypto.enc.Hex);
    const payload = {
      data: [
        {
          event_name: 'Purchase',
          event_time: Math.floor(new Date().getTime() / 1000),
          action_source: 'website',
          user_data: {
            em: [hashedEmail],
            ph: [null],
            client_ip_address: ipAddress,
            client_user_agent: userAgent
          },
          custom_data: {
            currency: 'EUR',
            value: '0.50'
          }
        }
      ]
    };

    this.fbPixelService.sendEvent(payload).subscribe(response => {
    }, error => {
      console.error('Errore nell\'invio dell\'evento:', error);
    });
  }

  sendSubscribeEvent(ipAddress: string, userAgent: string): void {
      const hashedEmail = crypto.SHA256(this.email).toString(crypto.enc.Hex);
      const payload = {
        data: [
          {
            event_name: 'Subscribe',
            event_time: Math.floor(new Date().getTime() / 1000),
            action_source: 'website',
            user_data: {
              em: [hashedEmail],
              ph: [null],
              client_ip_address: ipAddress,
              client_user_agent: userAgent
            },
            custom_data: {
              currency: 'EUR',
              value: '39.99'
            }
          }
        ]
      };

      this.fbPixelService.sendEvent(payload).subscribe(response => {
      }, error => {
        console.error('Errore nell\'invio dell\'evento:', error);
      });
  }

}
